body {
  margin: 0;
  min-height: 100vh;
  background-color: #3F3D56;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.map-wrapper svg {
  max-height: 70vh;
  position: absolute;
  top: 0;
  width: 100vw;
  min-height: 100vh;
  left: 0;
  stroke: transparent;
}
.map-wrapper svg path {
  background-color: rgb(224, 224, 224);
}
.customBottomPaper {
  padding: 1.5rem 3rem;
  position: absolute;
  bottom: 1rem;
  z-index: 10;
  width: -webkit-fill-available;
  background-color: transparent !important;
  color: white !important;
}
.MuiSlider-markLabel {
  color: white !important;
}
.on-boarding {
  position: absolute;
  width: 100%;
  height: 100vh;
  max-height: 100vh;
  top: 0;
  left: 0;
  margin: 0;
  background-color: #3f3d56;
  color: white;
  z-index: 1000;
}
.on-boarding-content {
  padding: 3rem;
  text-align: center;
}
.on-boarding svg {
  max-height: 25vh;
  max-width: 100%;
}
@media screen and (max-width: 640px) {
  .on-boarding svg {
    max-height: 17vh;
  }
}
.title-wrapper {
  padding: 1rem;
}
/* LEGEND */
.legend-item {
  width: 20px;
  height: 15px;
  margin-right: 15px;
  margin-bottom: 0;
}
.gradient-legend-item {
  width: 20px;
  margin-right: 15px;
  margin-bottom: 0;
  height: 50px;
}
.legend-item-wrapper {
  align-items: center;
}
.gradient-legend-item-wrapper {
  align-items: flex-start;
}
.legend-items-wrapper {
  /*transform: rotate(180deg)*/
}
.legends {
  position: absolute;
  top: 50%;
  padding-left: 1rem;
  transform: translateY(-50%);
  color: white;
}
/* HELPERS*/
.d-flex {
  display: flex;
}
.text-center {
  text-align: center;
}
.text-left {
  text-align: left;
}
.text-end {
  text-align: end;
}
.my-2 {
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.down-small {
  transform: translateY(6px);
}
.pb-mid {
  padding-bottom: 2rem;
}
.pt-high {
  padding-top: 3rem;
}
.mt-low {
  margin-top: 1rem !important;
}
.custom-tooltip {
  pointer-events: none;
}
.custom-tooltip::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

@media screen and (min-width: 320px) and (max-width: 767px) and (orientation: portrait) {
  .map-section {
    transform: rotate(-90deg);
    transform-origin: left top;
    width: 100vh;
    overflow-x: hidden;
    height: 100vw;
    position: absolute;
    top: 100%;
    left: 0;
  }
  .map-wrapper svg {
    position: absolute;
    top: 0;
    width: 80vh;
    min-height: 100vw;
    left: 20vw;
    stroke: transparent;
  }
  .legends {
    max-height: 30vh;
  }
  .gradient-legend-item {
    height: 30px;
  }
}
@media screen and (min-width: 320px) and (max-width: 767px) {
  .customBottomPaper {
    padding-bottom: 0;
  }
  #simple-wealth-info {
    transform: rotate(-90deg) translateY(-6px) translateX(44px);
  }
  .legend-items-wrapper .gradient-legend-item-wrapper:nth-child(even) span {
    display: none
  }
  /* LEGEND */
  .legend-item {
    margin-right: 5px;
  }
  .legend-item-wrapper {
    margin-right: 10px;
  }
  .legend-unity {
    margin-right: 15px !important;
    margin-bottom: 0 !important;
  }
}
.z-index-mid {
  z-index: 5;
}